.users {
  margin: 2rem auto;
  width: 90%;
  max-width: 40rem;
  list-style-type: none;
}

.users ul {
  list-style: none;
  padding: 1rem;
}

.users li {
  border: 1px solid #ccc;
  margin: 0.5rem 0;
  padding: 0.5rem;
}
  .input {
    margin: 2rem auto;
    padding: 1rem;
    width: 90%;
    max-width: 40rem;
  }
  
  .input label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .input input {
    font: inherit;
    display: block;
    width: 100%;
    border: 1px solid #ccc;
    padding: 0.15rem;
    margin-bottom: 0.5rem;
  }
  
  .input input:focus {
    outline: none;
    border-color: #096470;
  }
  .button {
    width: 100%;
    font: inherit;
    border: 1px solid #096470;
    background: #096470;
    color: white;
    padding: 0.25rem 1rem;
    cursor: pointer;
}
.button:hover, .button:active {
    background: #096470;
    border-color: #096470;
}
.button:focus {
    outline: none;
}