.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0, 0, 0, 0.75);
  }
  .modal {
    position: fixed;
    top: 30vh;
    left: 10%;
    width: 80%;
    z-index: 100;
    overflow: hidden;
  }
  .header {
  
    padding: 1rem;
  }
  .header h2 {
    margin: 0;
    color: #096470;
  }
  .content {
    padding: 1rem;
  }
  .actions {
    padding: 1rem;
    display: flex;
    justify-content: flex-end;
  }
  @media (min-width: 768px) {
    .modal {
      left: calc(50% - 20rem);
      width: 40rem;
    }
  }

  .button {
    font: inherit;
    border: 1px solid #096470;
    background: #096470;
    color: white;
    padding: 0.25rem 1rem;
    cursor: pointer;
}
.button:hover, .button:active {
    background: #096470;
    border-color: #096470;
}
.button:focus {
    outline: none;
}